 * @Author: Marlon
 * @Date: 2020-02-19 10:06:12
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-24 17:38:58
 * @Description: 
 -->
<template>
  <el-dialog
    :title="FDTitle"
    :visible.sync="dialogTableVisible"
    :width="FDWidth===''? null:`${FDWidth}px`"
    @close="close"
  >
    <el-form :model="form" class="TDForm">
      <template v-for="(item, index) in FDForm">
        <!--  -->
        <el-form-item
          v-if="item.type==='input'"
          :label="!!(item.label.name)?`${item.label.name}`:''"
          :label-width="`${item.label.width}px`"
          :key="index"
        >
          <el-input
            :disabled="FDIsLook"
            :style="`width:${Number(item.width)}px`"
            v-model="form[item.key]"
            autocomplete="off"
            :clearable="item.clearable"
            :show-password="item.showPassword"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item
          v-if="item.type==='select'"
          :label="!!(item.label.name)?`${item.label.name}`:''"
          :label-width="`${item.label.width}px`"
          :key="index"
        >
          <el-select
            :disabled="FDIsLook"
            filterable
            v-model="form[item.key]"
            :placeholder="item.placeholder"
            :multiple="(item.multiple && item.multiple.show) ? true:false"
            :collapse-tags="(item.multiple && item.multiple.collapseTags) ? true:false"
            :style="`width:${Number(item.width)}px`"
          >
            <el-option
              v-for="(items, i) in item.option"
              :key="i"
              :label="items.label"
              :value="items.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!--  -->
        <el-form-item
          v-if="item.type==='textarea'"
          :label="!!(item.label.name)?`${item.label.name}`:''"
          :label-width="`${item.label.width}px`"
          :key="index"
        >
          <el-input
            :disabled="FDIsLook"
            :style="`width:${Number(item.width)}px`"
            type="textarea"
            :autosize="{ minRows: item.autosize.min, maxRows: item.autosize.min}"
            :placeholder="item.placeholder"
            v-model="form[item.key]"
            :show-word-limit="!!item.width"
          ></el-input>
        </el-form-item>
        <!-- (2) -->
        <el-form-item
          v-if="(item.type==='input-2')&&(item.inputItem.length)"
          :label="!!(item.label.name)?`${item.label.name}`:''"
          :label-width="`${item.label.width}px`"
          :key="index"
        >
          <ul class="input-n">
            <li v-for="(items, i) in item.inputItem" :key="i">
              <el-tooltip
                v-if="!!(items.tip)"
                class="item"
                effect="dark"
                :content="items.tip"
                placement="top"
              >
                <label for v-if="items.label">{{items.label}}</label>
              </el-tooltip>
              <label v-else for>{{items.label?`${items.label}`:''}}</label>
              <el-input-number
                :disabled="FDIsLook"
                v-if="items.input.type==='number'"
                :style="`width:${Number(items.input.width)}px`"
                controls-position="right"
                v-model="form[item.key]"
              ></el-input-number>
              <el-select
                v-else-if="items.input.type==='select'"
                :disabled="FDIsLook"
                filterable
                v-model="form[item.key]"
                :placeholder="items.input.placeholder"
                :multiple="items.input.multiple ? items.input.multiple.show:null"
                :collapse-tags="items.input.multiple ? items.input.multiple.collapseTags:null"
                :style="`width:${Number(items.input.width)}px`"
              >
                <el-option
                  v-for="(itemss, i) in items.input.option"
                  :key="i"
                  :label="itemss.label"
                  :value="itemss.value"
                ></el-option>
              </el-select>
              <el-input
                v-else
                :disabled="FDIsLook"
                :style="`width:${Number(items.input.width)}px`"
                v-model="form[item.key]"
                autocomplete="off"
              ></el-input>
            </li>
          </ul>
        </el-form-item>
        <!--  -->
        <el-form-item
          v-if="item.type==='radio'"
          :label="!!(item.label.name)?`${item.label.name}`:''"
          :label-width="`${item.label.width}px`"
          :key="index"
          style="text-align: center;"
        >
          <el-radio-group v-model="form[item.key]">
            <el-radio
              v-for="(items, i) in item.radioItem"
              :disabled="FDIsLook"
              :key="i"
              :label="items.value"
            >{{items.label}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <!--  -->
        <!--  -->
        <el-form-item
          v-if="item.type==='area-type'"
          :label="!!(item.label.name)?`${item.label.name}`:''"
          :label-width="`${item.label.width}px`"
          :key="index"
          style="text-align: center;"
        >
          <div class="area-type">
            <el-form-item
              v-for="(items, i) in item.list"
              :key="i"
              :label-width="`${items.label.width}px`"
              :label="!!(items.label.name)?`${items.label.name}`:''"
            >
              <el-select
                v-model="form[item.key]"
                :disabled="FDIsLook"
                :placeholder="items.selectItem[0].placeholder"
                class="area-type-list"
              >
                <el-option
                  v-for="itemss in items.selectItem[0].option"
                  :key="itemss.value"
                  :label="itemss.label"
                  :value="itemss.value"
                ></el-option>
              </el-select>
              <span class="area-type-"></span>
              <el-select
                v-model="form[item.key]"
                :disabled="FDIsLook"
                :placeholder="items.selectItem[1].placeholder"
                class="area-type-list"
              >
                <el-option
                  v-for="itemss in items.selectItem[1].option"
                  :key="itemss.value"
                  :label="itemss.label"
                  :value="itemss.value"
                ></el-option>
              </el-select>
              <i
                v-if="!FDIsLook"
                class="el-icon-circle-close area-type-icon-del"
                @click="areaEdit('del',item.list,i)"
              ></i>
              <i
                class="el-icon-circle-plus-outline area-type-icon-add"
                v-if="(!FDIsLook) && ((item.list.length-1) ===i)"
                @click="areaEdit('add',item.list,i)"
              ></i>
            </el-form-item>
          </div>
        </el-form-item>
      </template>
    </el-form>
    <div slot="footer" class="dialog-footer" v-if="!FDIsLook">
      <template v-for="(item, index) in FDFoot.button">
        <!--  -->
        <el-button
          :key="index"
          v-if="item.type==='ok'"
          type="primary"
          @click="FDEdit(item)"
        >{{item.label}}</el-button>
        <!--  -->
        <el-button :key="index" v-if="item.type==='button'" @click="FDEdit(item)">{{item.label}}</el-button>
      </template>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    // 
    FDTitle: {
      type: String,
      default: ""
    },
    // 
    FDVisible: {
      type: Boolean,
      default: false
    },
    FDWidth: {
      type: String,
      default: ""
    },
    FDForm: {
      type: Array,
      default: function() {
        return [
          {
            type: "input",
            label: {
              name: "",
              width: "auto"
            },
            width: "120",
            defaultModel: "",
            placeholder: ""
          },
          {
            type: "select",
            label: {
              name: "",
              width: "auto"
            },
            width: "120",
            defaultModel: "",
            placeholder: ""
          },
          {
            type: "textarea",
            label: {
              name: "",
              width: "auto"
            },
            width: "80",
            defaultModel: "",
            placeholder: "",
            autosize: {
              min: 2,
              max: 4
            }
          }
        ];
      }
    },
    // 
    FDIsLook: {
      type: Boolean,
      default: false
    },
    // 
    FDFoot: {
      type: Object,
      default: function() {
        return {
          type: "submit",
          button: [
            // {
            //   type: "button",
            //   label: ""
            // },
            {
              type: "ok",
              label: ""
            }
          ]
        };
      }
    }
  },

  data() {
    return {
      // 
      dialogTableVisible: false,
      // 
      form: {},
    };
  },
  watch: {
    FDVisible(newVal, oldVal) {
      this.dialogTableVisible = newVal;
    },
    FDForm: {
      handler(newVal, oldVal) {
        // 
        let obj_ = {};
        if (newVal.length) {
          newVal.forEach(item => {
            if (item.key) {
              obj_[item.key] = item.defaultModel;
            }
          });
          this.form = Object.assign({}, this.form, obj_);
          console.log(this.form, "keyform");
        }
      }
      //   deep: true,
      //   immediate: true
    }
  },
  methods: {
    // 
    FDEdit(edit) {
      if (edit.cb) {
        edit.cb(this.form);
      } else {
        console.error("(cb)");
      }
    },
    // 
    close() {
      this.$emit("editBtnCb", {
        label: "close",
        close: this.dialogTableVisible
      });
    },
    // ()
    areaEdit(type, list, i) {
      if (type === "del") {
        if (list.length) {
          if (list.length - 1 !== 0) {
            list = list.splice(i, 1);
          }
        }
      } else {
        let dom = {
          label: {
            name: "",
            width: "120"
          },
          selectItem: [
            {
              placeholder: "",
              option: [
                {
                  label: "",
                  value: ""
                }
              ]
            },
            {
              placeholder: "",
              option: [
                {
                  label: "",
                  value: ""
                }
              ]
            }
          ]
        };
        list.push(dom);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
// input
.TDForm {
  .input-n {
    overflow: hidden;
    display: flex;
    margin: 0 auto;
    text-align: center;
    & > li {
      float: left;
      flex: 1;
    }
  }
  .area-type {
    width: 100%;
    max-height: 126px;
    box-sizing: border-box;
    //   border: 1px solid red;
    overflow-x: hidden;
    overflow-y: auto;
    &-list {
      width: 35%;
      float: left;
    }
    &- {
      width: 10%;
      float: left;
    }
    &-icon-add {
      margin-right: 5px;
      font-size: 20px;
      cursor: pointer;
      margin-top: 10px;
      &:hover {
        color: green;
      }
    }
    &-icon-del {
      font-size: 20px;
      cursor: pointer;
      margin-top: 10px;
      &:hover {
        color: red;
      }
    }
  }
}
</style>


