 <template>
  <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
    <textarea ref="mycode" class="codesql" v-model="code" style="height:200px;width:600px;"></textarea>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="dialogVisible = false"> </el-button>
    </span>
  </el-dialog>
</template>
 
 <script>
// import * as monaco from "monaco-editor";
import sqlFormatter from "sql-formatter";
// import MonacoEditor from "vue-monaco-editor";
import "codemirror/theme/ambiance.css";
import "codemirror/lib/codemirror.css";
import "codemirror/addon/hint/show-hint.css";
let CodeMirror = require("codemirror/lib/codemirror");
require("codemirror/addon/edit/matchbrackets");
require("codemirror/addon/selection/active-line");
require("codemirror/mode/sql/sql");
require("codemirror/addon/hint/show-hint");
require("codemirror/addon/hint/sql-hint");

export default {
  name: "sqlEditor",
  components: {
    // MonacoEditor
  },
  data() {
    return {
      dialogVisible: true,
      code: "//Ctrl",
      options: {
        selectOnLineNumbers: false
      }
    };
  },
  methods: {
    initEditor() {
      let _this = this;
      _this.$refs.sqlContainer.innerHTML = "";
      //   _this.monacoEditor = monaco.editor.create(_this.$refs.sqlContainer, {
      //     value: "113",
      //     language: "sql",
      //     wrappingColumn: 0,
      //     wrappingIndent: "indent",
      //     autoIndent: true,
      //     folding: true,
      //     foldingStrategy: "indentation",
      //     minimap: {
      //       enabled: false
      //     }
      //   });
      //   _this.monacoEditor.onDidChangeModelContent(function(event) {
      //     console.log(_this.monacoEditor.getValue());
      //   });
      //
    },
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    onMounted(editor) {
      this.editor = editor;
    },
    onCodeChange(editor) {
      console.log(editor.getValue());
    }
  },
  mounted() {
    this.$nextTick(() => {
      console.log(this.$refs);
      let editor = CodeMirror.fromTextArea(this.$refs.mycode, {
        mode: 'text/x-sql', //
        indentWithTabs: true,
        smartIndent: true,
        lineNumbers: true,
        matchBrackets: true,
        theme: "ambiance",
        lineWrapping: true,
        autofocus: true,
        extraKeys: { Ctrl: "autocomplete" }, //
        // readOnly:true,
        undoDepth: 2000000,
        gutters: [
          "CodeMirror-linenumbers",
          "CodeMirror-foldgutter",
          "CodeMirror-lint-markers"
        ],
        foldGutter: true, // 
        autoCloseBrackets: true, // 
        // styleActiveLine: true // 
        // hintOptions: {
        //   //
        //   tables: {
        //     users: ["name", "score", "birthDate"],
        //     countries: ["name", "population", "size"]
        //   }
        // }
      });
      console.log(CodeMirror.modes,CodeMirror.mimeModes)
      //cursorActivitychange
      editor.on("cursorActivity", function() {
        editor.showHint();
      });
    });
  }
};
</script>
 
 <style>
</style>



