 * @Author: Marlon
 * @Date: 2020-02-18 21:59:18
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-25 21:15:23
 * @Description: 
 -->
 <template>
  <div class="T-table">
    <el-table
      style="width: 100%"
      tooltip-effect="dark"
      :height="THeight===0 ?null: THeight"
      :max-height="TMaxHeight ===0 ?null:TMaxHeight"
      :ref="TId"
      :border="TBorder"
      v-loading="TLoading"
      :data="TData"
      :row-class-name="TMarkAverageFn"
      :cell-class-name="TMarkMinMaxLinkFn"
      :summary-method="TSumFn"
      @selection-change="handleSelectionChange"
      @cell-click="TClickCells"
      @row-click="TClickRowFn"
    >
      <!--  -->
      <el-table-column v-if="TIsShowCheckBox" type="selection" width="35" :fixed="true"></el-table-column>
      <!--  -->
      <el-table-column
        v-if="TIsShowIndex"
        label=""
        type="index"
        :index="TindexFn"
        width="50"
        align="center"
        :fixed="true"
      ></el-table-column>
      <!--  -->
      <template v-for="(column, index) in TTitle">
        <el-table-column
          align="center"
          :key="index"
          :label="column.label"
          :prop="column.prop"
          :show-overflow-tooltip="TOverflowTooltip"
          :sortable="TIsSort"
        >
          <template v-if="column.child">
            <el-table-column
              v-for="(child , indexs) in column.child"
              align="center"
              :key="indexs"
              :label="child.label"
              :prop="child.prop"
              :show-overflow-tooltip="TOverflowTooltip"
              :sortable="TIsSort"
            ></el-table-column>
          </template>
        </el-table-column>
      </template>
      <!--  -->
      <el-table-column
        v-if="TEdit.show"
        :label="TEdit.title"
        :width="TEdit.width || null"
        :fixed="TEdit.fixed || null"
        align="center"
      >
        <template v-for="(item, index) in TEdit.editTypes">
          <el-button
            :key="index"
            size="mini"
            :class="editButtonClassFn(item)"
            @click="TEditFn(item)"
          >{{item.label}}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      v-if="TIsShowPage"
      style="text-align:center;"
      layout="total, sizes, prev, pager, next, jumper"
      class="pagination"
      :current-page="currentPage"
      :page-sizes="[10, 20]"
      :page-size="pageSize"
      :total="TTotal"
      @size-change="TChangePageSizeFn"
      @current-change="TChangeCurrentFn"
    ></el-pagination>
  </div>
</template>
 
 <script>
import { cellJump } from "utils/jump";
export default {
  props: {
    //  id
    TId: {
      type: String,
      default: ""
    },
    // 
    TLoading: {
      type: Boolean,
      default: false
    },
    // 
    TTitle: {
      type: Array,
      default: function() {
        return [];
      }
    },
    // 
    TData: {
      type: Array,
      default: function() {
        return [];
      }
    },
    // 
    TIsShowIndex: {
      type: Boolean,
      default: true
    },
    // 
    TIsSort: {
      type: Boolean,
      default: false
    },
    // 
    TClickTable: {
      type: Array,
      default: function() {
        return [];
      }
    },
    // 
    TIsShowPage: {
      type: Boolean,
      default: false
    },
    // 
    TTotal: {
      type: Number,
      default: 0
    },
    // 
    TCurrentPage: {
      type: Number,
      default: 1
    },
    // 
    TPageSize: {
      type: Number,
      default: 10
    },
    // 
    TBorder: {
      type: Boolean,
      default: true
    },
    // 
    TMaxHeight: {
      type: Number,
      default: 0
    },
    // 
    THeight: {
      type: Number,
      default: 0
    },
    // 
    TIsShowCheckBox: {
      type: Boolean,
      default: false
    },
    // 
    TOverflowTooltip: {
      type: Boolean,
      default: true
    },
    // 
    TEdit: {
      type: Object,
      default: function() {
        return {
          show: false,
          title: "",
          editTypes: [
            {
              label: "",
              css: "",
              property: "",
              pass: true // true() false:
            },
            {
              label: "",
              css: "",
              property: "",
              pass: true
            }
          ]
        };
      }
    },
    // 
    TClickItem: {
      type: Array,
      default: function() {
        return [];
      }
    }
    //
    // // 
    // TSum: {
    //   type: Array,
    //   default: function() {
    //     return [];
    //   }
    // }
  },
  data() {
    return {
      multipleSelection: [], // 
      currentPage: 1, // 
      pageSize: 10, // 
      editButtonClass: [],
      clickEditBtnObj: "", //
      index_: 0,
      row_: null
    };
  },
  watch: {
    TCurrentPage(newVal) {
      this.currentPage = newVal;
    },
    TPageSize(newVal) {
      this.pageSize = newVal;
    }
  },
  methods: {
    // 
    TChangePageSizeFn(size) {
      this.$emit("changePage", {
        pageSize: size,
        currentPage: this.currentPage
      });
    },
    // 
    TChangeCurrentFn(currentPage) {
      this.$emit("changePage", {
        pageSize: this.pageSize,
        currentPage: currentPage
      });
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    TMarkAverageFn({ row, rowIndex }) {
      //   if (
      //     (row.countName === "" ||
      //       row.countName === "" ||
      //       row.col_1 === "" ||
      //       row.groupTime === "") &&
      //     this.isNeedOtherData
      //   ) {
      //     return "average-row";
      //   }
      //   return "";
    },
    //   
    TMarkMinMaxLinkFn({ row, column, rowIndex, columnIndex }) {
      let clickCell = column.property || "";
      if (clickCell) {
        if (this.TClickItem.length) {
          let linkArr = [];
          let sign = "";
          for (let index = 0; index < this.TClickItem.length; index++) {
            if (
              this.TClickItem[index].cells &&
              this.TClickItem[index].cells.length
            ) {
              if (this.TClickItem[index].cells.includes(clickCell)) {
                // TODO 
                if (this.TClickItem[index].type === "dialog") {
                  sign = "clickStyle-dialog";
                } else if (this.TClickItem[index].link) {
                  sign = "clickStyle";
                }
                return sign;
              }
            }
          }
        }
      }
    },
    // 
    multistageTable() {
      return this.totalNum;
    },
    // 
    TindexFn(index) {
      return (this.currentPage - 1) * this.pageSize + index + 1;
    },
    // 
    TSumFn() {
      return [];
    },
    // TODO 
    editButtonClassFn(item) {
      let BtnClass = "";
      this.TEdit.editTypes.forEach((element, index) => {
        if (item.label === element.label) {
          BtnClass = element.css || "";
          return;
        } else {
          return BtnClass;
        }
      });

      return BtnClass;
    },
    // 
    TEditFn(editItem) {
      this.index_ += 1;
      this.clickEditBtnObj = editItem;
      if (!editItem.pass) {
        if (editItem.label === "") {
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              this.$emit("editBtnCb", editItem, this.TId, this.row_);
            })
            .catch(() => {});
          return;
        }
      }
    },
    // 
    TClickCells(row, column, cell, event) {
      // console.log(row, column, cell, event, "");
      let colClink = column.property;
      //   ()
      let Callback_ = null;
      // ()
      //   console.log(this.clickEditBtnObj);
      if (this.clickEditBtnObj && this.clickEditBtnObj.link) {
        //   
        let TClickItem_ = [
          {
            link: this.clickEditBtnObj.link,
            jump: this.clickEditBtnObj.jump || null
          }
        ];
        console.log(this.clickEditBtnObj);
        // 
        cellJump(
          this,
          {
            row,
            column,
            TClickItem: TClickItem_
          },
          Callback_
        );

        return;
      }
      // ()
      if (this.TClickItem.length) {
        if (!row[colClink]) return;
        for (let index = 0; index < this.TClickItem.length; index++) {
          // 
          if (
            this.TClickItem[index].type &&
            this.TClickItem[index].type === "dialog"
          ) {
            // 
            Callback_ = this.TClickItem[index]["cb"];
          }
          if (this.TClickItem[index].cells.length) {
            let clickItem_ = this.TClickItem[index].cells;
            for (let j = 0; j < clickItem_.length; j++) {
              if (colClink === clickItem_[j]) {
                // 
                cellJump(
                  this,
                  {
                    row,
                    column,
                    TClickItem: this.TClickItem
                  },
                  Callback_
                );
              }
            }
          }
        }
      }
    },
    TClickRowFn(row, column, event) {
      this.index_ += 1;
      if (this.index_ % 2 !== 0) {
        this.$emit("clickRow", { row, column, id: this.TId });
      } else {
        // 
        this.row_ = row;
        let column_ = Object.assign({}, column, this.clickEditBtnObj);
        // ()
        this.$emit("clickCell", { row, column: column_, id: this.TId });
      }
      this.index_ = 0;
    }
  }
};
</script>
 
<style lang="scss" scoped>
@import "~@/assets/css/platform/gst/config.scss";
.T-table {
  margin: 10px 0;
  //   .el-button:focus,
  //   .el-button:hover {
  //     color: #01c1df;
  //     border-color: #01c1df;
  //     background-color: rgba(1, 193, 223, 0.1);
  //   }
  /deep/ .el-table {
    th {
      background-color: $table_title_bg;
    }
    thead {
      color: $table_title_color;
    }
    // -
    .clickStyle {
      text-decoration: underline;
      color: $table_link_color;
      cursor: pointer;
      &:hover {
        color: $table_link_color_hover;
      }
      //   
      &-dialog {
        color: $table_link_color;
        cursor: pointer;
        &:hover {
          color: $table_link_color_hover;
        }
      }
    }
  }
  //  
  .pagination {
    margin: 15px 0;
  }
  // TODO 
  .edit-button-color {
    &-1 {
      background: $table_edit_btn_bg;
      color: $table_edit_btn_color;
      &:hover,
      &:focus {
        color: $table_edit_btn_color_focus;
        border-color: $table_edit_btn_border_color;
        background-color: $table_edit_btn_bg_focus;
      }
      &:active {
        color: $table_edit_btn_color_active;
        border-color: $table_edit_btn_border_color_active;
        outline: 0;
      }
    }
  }
}
</style>



