import { Message } from "element-ui";

/* 
    
    type{
                href:();
                open:;
                replace:URL;
                assign:URLhref
            }
*/
export const openUrl = ({ type = 'href', url = '', obj = null }) => {
    let params = '';
    if (obj && JSON.stringify(obj) !== "{}") {
        if (!Array.isArray(obj)) {
            for (const key in obj) {
                if (obj.hasOwnProperty(key)) {
                    params += "&" + key + "=" + obj[key];
                }
            }
            if (!!params) {
                params = params.replace("&", "?");
            }
        }
    }
    switch (type) {
        case 'href':
            window.location.href = url + params
            return;
        case 'open':
            window.open(url + params)
            return;
        case 'replace':
            window.location.replace(url + params)
            return;
        case 'assign':
            window.localtion.assign(url + params)
            return;
        default:
            Message({
                showClose: true,
                message: ':(href/open/replace/assign)',
                type: "error"
            });
            return;
    }
}

/* 
    
*/
export const cellJump = (vm, { row, column, TClickItem = [] }, cb) => {
    // console.log(row, column, TClickItem)
    let clickCell = column.property || "";
    if (TClickItem.length) {
        for (let index = 0; index < TClickItem.length; index++) {
            if (TClickItem[index].cells && Array.isArray(TClickItem[index].cells)) {
                // 
                if (TClickItem[index].cells.length && TClickItem[index].cells.includes(clickCell)) {
                    // 
                    if (TClickItem[index].link) {// 
                        if (TClickItem[index].openType) {
                            let openType = TClickItem[index].openType;
                            switch (openType) {
                                case "blank":
                                    let routeData = vm.$router.resolve({
                                        path: TClickItem[index].link,
                                        query: {
                                            // 'itemInfo': JSON.stringify(pathInfo),
                                            // 'currIndex': currSider
                                        },
                                        params: {
                                            row: JSON.stringify(row),
                                            column: JSON.stringify(column),
                                        }
                                    });
                                    window.open(routeData.href, "_blank");
                                    break;
                                case "replace":
                                    vm.$router.replace({
                                        path: TClickItem[index].link,
                                        params: {
                                            row: JSON.stringify(row),
                                            column: JSON.stringify(column),
                                        }
                                    })
                                    break;
                                default:
                                    break;
                            }
                        } else {
                            vm.$router.push({
                                path: TClickItem[index].link,
                                query: {
                                    // row:JSON.stringify(row),
                                    // column:JSON.stringify(column)
                                },
                                params: {
                                    row: JSON.stringify(row),
                                    column: JSON.stringify(column),
                                }
                            })
                        }
                    } else if (TClickItem[index].type === 'dialog') {
                        cb()
                    }
                }
            } else {
                // 
                if (TClickItem[index].jump) {
                    const { type, mode } = TClickItem[index].jump
                    if (type === "out") {
                        openUrl({ type: mode, url: TClickItem[index].link, obj: { row: JSON.stringify(row), column: JSON.stringify(column) } })
                    } else {
                        // 
                        switch (mode) {
                            case "blank":
                                let routeData = vm.$router.resolve({
                                    path: TClickItem[index].link,
                                    query: {
                                        // 'itemInfo': JSON.stringify(pathInfo),
                                        // 'currIndex': currSider
                                    },
                                    params: {
                                        row: JSON.stringify(row),
                                        column: JSON.stringify(column),
                                    }
                                });
                                window.open(routeData.href, "_blank");
                                return;
                            case "replace":
                                vm.$router.replace({
                                    path: TClickItem[index].link,
                                    params: {
                                        row: JSON.stringify(row),
                                        column: JSON.stringify(column),
                                    }
                                });
                                return;
                            default:
                                // push
                                vm.$router.push({
                                    path: TClickItem[index].link,
                                    query: {
                                        // row:JSON.stringify(row),
                                        // column:JSON.stringify(column)
                                    },
                                    params: {
                                        row: JSON.stringify(row),
                                        column: JSON.stringify(column),
                                    }
                                });
                                break;
                        }
                    }
                } else {
                    console.error(':')
                }
            }
        }
    }

}




