import service from 'utils/request'

import API from './public'

/*  */
const BaseUrl = '/dsm'

export default {
    // 
    _PublicDataBaseList(data) {
        return service({
            url: BaseUrl + '/dict',
            method: 'get',
            data
        })
    },
    /* ******************************* */
    // 
    DSMCList(data) {
        return service({
            url: BaseUrl + '/ds/page',
            method: 'get',
            data
        })
    },
    // 
    DSMCAdd(data) {
        return service({
            url: BaseUrl + '/ds',
            method: 'post',
            data
        })
    },
    // 
    DSMCDel(data) {
        return service({
            url: BaseUrl + '/ds',
            method: 'DELETE',
            data
        })
    },
    // 
    DSMCUpdate(data) {
        return service({
            url: BaseUrl + '/ds',
            method: 'put',
            data
        })
    }
}


